<template>
  <v-stepper v-model="step">
    <v-stepper-header>
      <v-stepper-step :complete="step > 1" step="1"></v-stepper-step>
      <v-divider/>
      <v-stepper-step :complete="step > 2" step="2"></v-stepper-step>
      <v-divider/>
      <v-stepper-step :complete="step > 3" step="3"></v-stepper-step>
      <v-divider/>
      <v-stepper-step step="4">SKU</v-stepper-step>
    </v-stepper-header>
    <v-stepper-items>
      <!--1-->
      <v-stepper-content step="1">
        <v-flex class="xs10 mx-auto">
          <v-form v-model="valid" ref="basic">
            <v-layout row>
              <v-flex xs5>
                <!---->
                <v-cascader
                  url="/item/category/of/parent"
                  required
                  showAllLevels
                  v-model="goods.categories"
                  label=""/>
              </v-flex>
              <v-spacer/>
              <v-flex xs5>
                <!---->
                <v-autocomplete
                  :items="brandOptions"
                  item-text="name"
                  item-value="id"
                  label=""
                  v-model="goods.brandId"
                  required
                  autocomplete
                  clearable
                  dense chips
                  :rules="[v => !!v || '']"
                >
                  <template slot="selection" slot-scope="data">
                    <v-chip small>{{ data.item.name}}</v-chip>
                  </template>
                </v-autocomplete>
              </v-flex>
            </v-layout>
            <v-text-field label="" v-model="goods.name" :counter="200" required :rules="[v => !!v || '']" hide-details/>
            <v-text-field label="" v-model="goods.subTitle" :counter="200" hide-details/>
            <v-textarea label="" v-model="goods.spuDetail.packingList" :counter="1000" :rows="3" hide-details/>
            <v-textarea label="" v-model="goods.spuDetail.afterService" :counter="1000" :rows="3" hide-details/>
          </v-form>
        </v-flex>
      </v-stepper-content>
      <!--2-->
      <v-stepper-content step="2">
        <v-editor v-model="goods.spuDetail.description" url="/upload/signature" needSignature/>
      </v-stepper-content>
      <!--3-->
      <v-stepper-content step="3">
        <v-flex class="xs10 mx-auto px-3">
          <!---->
          <v-card class="my-2">
            <v-container grid-list-md fluid>
            <v-layout wrap row justify-space-between class="px-5">
              <v-flex xs12 sm5 v-for="param in specs" :key="param.name">
                <v-text-field :label="param.name" v-model="param.v" :suffix="param.unit || ''"
                 />
              </v-flex>
            </v-layout>
          </v-container>
          </v-card>
        </v-flex>
      </v-stepper-content>
      <!--4SKU-->
      <v-stepper-content step="4">
        <v-flex class="mx-auto">
          <!---->
          <v-card flat v-for="spec in specialSpecs" :key="spec.name">
            <!---->
            <div class="subheading">{{spec.name}}:</div>
            <!--options-->
            <v-card-text class="px-5">
              <div v-for="i in spec.options.length+1" :key="i" class="layout row px-5">
                <v-text-field :placeholder="'' + spec.name + ':'" class="flex xs10" auto-grow
                              v-model="spec.options[i-1]" v-bind:value="i" single-line hide-details/>

                <v-btn @click="spec.options.splice(i-1,1)" v-if="i <= spec.options.length" icon>
                  <i class="el-icon-delete"/>
                </v-btn>
              </div>
            </v-card-text>
          </v-card>
          <v-card class="elevation-0">
            <!---->
            <div class="subheading py-3">SKU:</div>
            <v-divider/>
            <!--SKUhide-actions-->
            <v-data-table :items="skus" :headers="headers" hide-actions item-key="indexes" class="elevation-0">
              <template slot="items" slot-scope="props">
                <tr @click="props.expanded = !props.expanded">
                  <!---->
                  <td v-for="(v,k) in props.item" :key="k" v-if="['price', 'stock'].includes(k)"
                      class="text-xs-center">
                    <v-text-field single-line v-model="props.item[k]" @click.stop=""/>
                  </td>
                  <!--enablecheckbox-->
                  <td class="text-xs-center" v-else-if="k === 'enable'">
                    <v-checkbox v-model="props.item[k]"/>
                  </td>
                  <!--indexesimages-->
                  <td class="text-xs-center" v-else-if="k !== 'images' && k !== 'indexes'">{{v.v}}</td>
                </tr>
              </template>
              <!---->
              <template slot="expand" slot-scope="props">
                <v-card class="elevation-2 flex xs11 mx-auto my-2">
                  <!---->
                  <v-upload v-model="props.item.images" url="/upload/signature" needSignature/>
                </v-card>
              </template>
            </v-data-table>
          </v-card>
        </v-flex>
        <!---->
        <v-flex xs3 offset-xs9>
          <v-btn color="info" @click="submit"></v-btn>
        </v-flex>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
export default {
  name: "goods-form",
  props: {
    oldGoods: {
      type: Object
    },
    isEdit: {
      type: Boolean,
      default: false
    },
    step: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      valid:false,
      goods: {
        categories: [], // 
        brandId: 0, // id
        name: "", // 
        subTitle: "", // 
        spuDetail: {
          packingList: "", // 
          afterService: "", // 
          description: "" // 
        }
      },
      brandOptions: [], // 
      specs: [], // 
      specialSpecs: [] // 
    };
  },
  methods: {
    submit() {
      // 
      if(!this.$refs.basic.validate){
        this.$message.error("");
      }
      // goods,categoriesgoodsParams
      const {
        categories: [{ id: cid1 }, { id: cid2 }, { id: cid3 }],
        ...goodsParams
      } = this.goods;
      // 
      const specs = {};
      this.specs.forEach(({ id,v }) => {
        specs[id] = v;
      });
      // 
      const specTemplate = {};
      this.specialSpecs.forEach(({ id, options }) => {
        specTemplate[id] = options;
      });
      // sku
      const skus = this.skus
        .filter(s => s.enable)
        .map(({ price, stock, enable, images, indexes, ...rest }) => {
          // spuname
          const title = goodsParams.name + " " + Object.values(rest).map(v => v.v).join(" ");
          const obj = {};
          Object.values(rest).forEach(v => {
            obj[v.id] = v.v;
          });
          return {
            price: this.$format(price), // 
            stock,
            indexes,
            enable,
            title, // 
            images: images ? images.join(",") : '', // 
            ownSpec: JSON.stringify(obj) // 
          };
        });
      Object.assign(goodsParams, {
        cid1,
        cid2,
        cid3, // 
        skus // sku
      });
      goodsParams.spuDetail.genericSpec = JSON.stringify(specs);
      goodsParams.spuDetail.specialSpec = JSON.stringify(specTemplate);
      this.$http({
        method: this.isEdit ? "put" : "post",
        url: "/item/goods",
        data: goodsParams
      })
        .then(() => {
          // 
          this.$emit("close");
          // 
          this.$message.success("");
        })
        .catch(() => {
          this.$message.error("");
        });
    }
  },
  watch: {
    oldGoods: {
      deep: true,
      handler(val) {
        if (!this.isEdit) {
          Object.assign(this.goods, {
            categories: null, // 
            brandId: 0, // id
            name: "", // 
            subTitle: "", // 
            spuDetail: {
              packingList: "", // 
              afterService: "", // 
              description: "" // 
            }
          });
          this.specs = [];
          this.specialSpecs = [];
        } else {
          this.goods = Object.deepCopy(val);

          // 
          const names = val.categoryName.split("/");
          delete this.goods.categoryName;
          delete this.goods.brandName;
          delete this.goods.createTime;
          delete this.goods.saleable;
          // 
          this.goods.categories = [
            { id: val.cid1, name: names[0] },
            { id: val.cid2, name: names[1] },
            { id: val.cid3, name: names[2] }
          ];

          // skusmap
          const skuMap = new Map();
          this.goods.skus.forEach(s => {
            s.enable = true;
            skuMap.set(s.indexes, s);
          });
          this.goods.skus = skuMap;
        }
      }
    },
    "goods.categories": {
      deep: true,
      handler(val) {
        // 
        if (val && val.length > 0) {
          // 
          this.$http
            .get("/item/brand/of/category?id=" + this.goods.categories[2].id)
            .then(({ data }) => {
              this.brandOptions = data;
            });
          // 
          this.$http
            .get("/item/spec/params?cid=" + this.goods.categories[2].id)
            .then(({ data }) => {
              let specs = [];
              let template = [];
              if (this.isEdit){
                specs = JSON.parse(this.goods.spuDetail.genericSpec);
                template = JSON.parse(this.goods.spuDetail.specialSpec);
              }
              // 
              const arr1 = [];
              const arr2 = [];
              data.forEach(({id, name,generic, numeric, unit }) => {
                if(generic){
                  const o = { id, name, numeric, unit};
                  if(this.isEdit){
                    o.v = specs[id];
                  }
                  arr1.push(o)
                }else{
                  const o = {id, name, options:[]};
                  if(this.isEdit){
                    o.options = template[id];
                  }
                  arr2.push(o)
                }
              });
              this.specs = arr1;// 
              this.specialSpecs = arr2;// 
            });
        }
      }
    }
  },
  computed: {
    skus() {
      // 
      const arr = this.specialSpecs.filter(s => s.options.length > 0);
      // reduce
      return arr.reduce(
        (last, spec, index) => {
          const result = [];
          last.forEach(o => {
            spec.options.forEach((option, i) => {
              const obj = JSON.parse(JSON.stringify(o));
              obj[spec.name] = {v:option, id:spec.id};
              obj.indexes = (obj.indexes || '') + '_' +  i
              if (index === arr.length - 1) {
                obj.indexes = obj.indexes.substring(1);
                // 
                Object.assign(obj, {
                  price: 0,
                  stock: 0,
                  enable: false,
                  images: []
                });
                if (this.isEdit) {
                  // sku
                  const sku = this.goods.skus.get(obj.indexes);
                  if (sku != null) {
                    const { price, stock, enable, images } = sku;
                    Object.assign(obj, {
                      price: this.$format(price),
                      stock,
                      enable,
                      images: images ? images.split(",") : [],
                    });
                  }
                }
              }
              result.push(obj);
            });
          });
          return result;
        },
        [{}]
      );
    },
    headers() {
      if (this.skus.length <= 0) {
        return [];
      }
      const headers = [];
      Object.keys(this.skus[0]).forEach(k => {
        let value = k;
        if (k === "price") {
          // enable
          k = "";
        } else if (k === "stock") {
          // enable
          k = "";
        } else if (k === "enable") {
          // enable
          k = "";
        } else if (k === "images" || k === 'indexes') {
          // 
          return;
        }
        headers.push({
          text: k,
          align: "center",
          sortable: false,
          value
        });
      });
      return headers;
    }
  }
};
</script>

