<template>
  <v-card>
    <v-toolbar class="elevation-0">
      <v-btn color="primary" @click="addGoods"></v-btn>
      <v-spacer/>
      <v-flex xs3>
        
        <v-btn-toggle mandatory v-model.lazy="saleable">
          <v-btn>
            
          </v-btn>
          <v-btn :value="true">
            
          </v-btn>
          <v-btn :value="false">
            
          </v-btn>
        </v-btn-toggle>
      </v-flex>
      <v-flex xs3>
        <v-text-field
            append-icon="search"
            label=""
            single-line
            hide-details
            v-model="search"
            @click:append="getDataFromServer"
        />
      </v-flex>
    </v-toolbar>
    <v-divider/>
    <v-data-table
        :headers="headers"
        :items="goodsList"
        :pagination.sync="pagination"
        :total-items="totalGoods"
        :loading="loading"
        class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <td class="text-xs-center">{{ props.item.id }}</td>
        <td class="text-xs-center">{{ props.item.name }}</td>
        <td class="text-xs-center">{{props.item.categoryName}}</td>
        <td class="text-xs-center">{{ props.item.brandName }}</td>
        <td class="justify-center layout px-0">
          <v-tooltip left>
            <v-btn slot="activator" icon @click="goodsDetail(props.item.id)">
              <i class="el-icon-search"/>
            </v-btn>
            <span> </span>
          </v-tooltip>
          <v-tooltip left>
            <v-btn slot="activator" icon @click="editGoods(props.item)">
              <i class="el-icon-edit"/>
            </v-btn>
            <span> </span>
          </v-tooltip>
          <v-tooltip left>
            <v-btn icon slot="activator">
              <i class="el-icon-delete"/>
            </v-btn>
            <span> </span>
          </v-tooltip>
          <v-tooltip left>
            <v-btn slot="activator" icon @click="updateSaleable(props.item.id, !props.item.saleable)">
              <i class="el-icon-upload2" v-if="!props.item.saleable"/>
              <i class="el-icon-download" v-else/>
            </v-btn>
            <span v-if="!props.item.saleable"> </span>
            <span v-else> </span>
          </v-tooltip>
        </td>
      </template>
    </v-data-table>
    <!---->
    <v-dialog max-width="800" v-model="show" persistent scrollable>
      <v-card>
        <!---->
        <v-toolbar dense dark color="primary">
          <v-toolbar-title>{{isEdit ? '' : ''}}</v-toolbar-title>
          <v-spacer/>
          <!---->
          <v-btn icon @click="closeWindow">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <!---->
        <v-card-text class="px-3" style="height: 600px">
          <goods-form :oldGoods="oldGoods" :step="step" @close="closeWindow" :is-edit="isEdit" ref="goodsForm"/>
        </v-card-text>
        <!---->
        <v-card-actions class="elevation-10">
          <v-flex class="xs3 mx-auto">
            <v-btn @click="previous" color="primary" :disabled="step === 1"></v-btn>
            <v-btn @click="next" color="primary" :disabled="step === 4"></v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
  // 
  import GoodsForm from './GoodsForm'

  export default {
    name: "goods",
    data() {
      return {
        saleable: true, // 
        search: '', // 
        totalGoods: 0, // 
        goodsList: [], // 
        loading: true, // 
        pagination: {}, // 
        headers: [
          {text: 'id', align: 'center', sortable: false, value: 'id'},
          {text: '', align: 'center', sortable: false, value: 'name'},
          {text: '', align: 'center', sortable: false, value: 'categoryName'},
          {text: '', align: 'center', value: 'brandName', sortable: false,},
          {text: '', align: 'center', sortable: false}
        ],
        show: false,// 
        oldGoods: {}, // 
        isEdit: false, // 
        step: 1, // 1
      }
    },
    mounted() { // 
      // 
      this.getDataFromServer();
    },
    watch: {
      pagination: { // pagination
        deep: true, // deeptruepagination
        handler() {
          // getDataFromServer
          this.getDataFromServer();
        }
      },
      saleable(){
        this.getDataFromServer();
      }
    },
    methods: {
      getDataFromServer() { // 
        // 
        this.$http.get("/item/spu/page", {
          params: {
            key: this.search, // 
            saleable: this.saleable === 0 ? null : this.saleable, // 
            page: this.pagination.page,// 
            rows: this.pagination.rowsPerPage,// 
          }
        }).then(resp => { // 
          this.goodsList = resp.data.items;
          this.totalGoods = resp.data.total;
          // false
          this.loading = false;
        }).catch(() => {
          this.goodsList = [];
          this.totalGoods = 0;
          // false
          this.loading = false;
        })
      },
      addGoods() {
        // 
        this.isEdit = false;
        // 
        this.show = true;
        // oldBrandnull
        this.oldGoods = {};
      },
      async editGoods(oldGoods) {
        if (oldGoods.saleable) {
          // 
          this.$message.error("");
          return;
        }
        // skus
        oldGoods.spuDetail = await this.$http.loadData("/item/spu/detail?id=" + oldGoods.id);
        oldGoods.skus = await this.$http.loadData("/item/sku/of/spu?id=" + oldGoods.id);
        // 
        this.isEdit = true;
        // 
        this.show = true;
        // goods
        this.oldGoods = oldGoods;
      },
      closeWindow() {
        console.log(1)
        // 
        this.getDataFromServer();
        // 
        this.show = false;
        // 1
        this.step = 1;
      },
      previous() {
        if (this.step > 1) {
          this.step--
        }
      },
      next() {
        if (this.step < 4 && this.$refs.goodsForm.$refs.basic.validate()) {
          this.step++
        }
      },
      updateSaleable(id, saleable) {
        this.$http.put("/item/spu/saleable", this.$qs.stringify({id, saleable}))
          .then(resp => {
            this.getDataFromServer();
            this.$message.success("");
          })
          .catch(error => {
            this.$message.error("");
          })
      },
      goodsDetail(id){
        this.$message.info("");
      }
    },
    components: {
      GoodsForm
    }
  }
</script>

